.container {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: linear-gradient(to right, #ff4b2b, #ff416c); /* Fondo degradado */
  }
  
  .formContainer {
    background-color: #ffffff;
    border-radius: 10px; /* Bordes redondeados */
    box-shadow: 0 5px 15px rgba(0, 0, 0, 0.2); /* Sombra */
    padding: 40px;
    width: 90%;
    max-width: 400px;
    text-align: center;
    transition: transform 0.2s, box-shadow 0.2s;
  }
  
  .formContainer:hover {
    transform: scale(1.02); /* Efecto sutil al pasar el mouse */
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.3); /* Sombra más intensa */
  }
  
  .formContainer h1 {
    font-size: 24px;
    color: #333333;
    margin-bottom: 20px;
  }
  
  .formContainer input {
    width: 100%;
    padding: 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    border-radius: 5px; /* Bordes redondeados para los inputs */
    font-size: 16px;
  }
  
  .formContainer button {
    background: #ff416c; /* Botón con el color original */
    color: white;
    border: none;
    padding: 10px 20px;
    font-size: 16px;
    border-radius: 5px; /* Bordes redondeados */
    cursor: pointer;
    transition: background 0.3s ease;
  }
  
  .formContainer button:hover {
    background: #ff4b2b; /* Cambio sutil de color al pasar el mouse */
  }
  
  .overlayContainer {
    display: none; /* Ocultamos este contenedor según el diseño */
  }
  
