.horarios-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  font-family: Arial, sans-serif;
}

.sidebar {
  flex: 0 0 250px;
  background-color: #3a3f51;
  padding: 20px;
  color: #fff;
  height: 100%;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
}

.horarios-content {
  flex: 1;
  padding: 20px;
  overflow-y: auto;
}

.grupo-section {
  margin-bottom: 30px;
}

.horarios-table-container {
  overflow-y: auto;
  max-height: 500px;
}

table {
  width: 100%;
  border-collapse: collapse;
}

th, td {
  border: 1px solid #ddd;
  padding: 10px;
  text-align: center;
}

th {
  background-color: #f0f0f0;
}

tbody tr:nth-child(odd) {
  background-color: #f9f9f9;
}

tbody tr:nth-child(even) {
  background-color: #ffffff;
}

tbody tr:hover {
  background-color: #f1f1f1;
}

td div {
  text-align: left;
}

td p {
  margin: 5px 0;
}

.grupo-section h2 {
  font-size: 1.3em;
  margin-bottom: 1em;
}
