    .container {
    display: flex; /* Configura flexbox para los hijos directos */
    height: 100vh; /* Asegura que ocupe toda la altura de la ventana */
    }

    .sidebar {
        flex: 0 0 250px; /* Ancho fijo de 250px para el aside */
        background-color: #f4f4f4; /* Color de fondo opcional */
        border-right: 1px solid #ddd; /* Línea divisoria */
        padding: 1rem; /* Espaciado interno */
        box-sizing: border-box;
      }
  
.content {
  flex: 1; /* Ocupa el resto del espacio disponible */
  padding: 1rem; /* Espaciado interno */
  box-sizing: border-box;
  overflow-y: auto; /* Agrega scroll si el contenido excede la altura */
}
  
  .title {
    font-size: 24px;
    margin-bottom: 20px;
  }
  
  .inputScrollContainer {
    display: flex;
    flex-wrap: wrap; /* Permite que los elementos fluyan a nuevas filas */
    gap: 1rem; /* Espaciado entre elementos */
    margin: 1rem 0; /* Espaciado vertical del contenedor */
  }
  
  .diaContainer {
    display: flex;
    flex-direction: column; /* Alinea los elementos en columna */
    align-items: flex-start; /* Alinea los elementos al inicio */
    border: 1px solid #ccc; /* Opcional: Añade un borde para visualización */
    padding: 1rem; /* Espaciado interno */
    min-width: 200px; /* Ancho mínimo para cada día */
    flex: 1 1 calc(33.33% - 1rem); /* Ajusta el ancho a un tercio del contenedor, con margen */
    box-sizing: border-box; /* Asegura que padding y borde no aumenten el ancho */
  }
  
  .horaContainer {
    display: flex;
    gap: 0.5rem; /* Espaciado entre las etiquetas de hora y sus inputs */
    flex-wrap: wrap; /* Permite que los inputs de hora se acomoden si no caben */
  }

  .checkboxGroup {
    margin-top: 20px;
    display: flex;
    gap: 20px;
  }
  