.horarios-container {
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow: hidden;
}

.sidebar {
  flex: 0 0 250px; /* Ancho fijo del aside */
  background-color: #f4f4f4;
  padding: 20px;
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1);
  height: 100vh; /* Asegura que el aside ocupe toda la altura */
  position: fixed; /* Hace que el aside permanezca fijo al lado izquierdo */
  left: 0;
  top: 0;
}

.columnas {
  flex: 1;
  padding: 20px;
  margin-left: 250px; /* Espacio para el aside fijo */
  overflow-y: auto; /* Permite el scroll si el contenido supera el espacio */
}

.header-section {
  display: flex;
  justify-content: space-between;
  gap: 20px;
}

.header-box {
  background: #ffffff;
  padding: 15px;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
  flex: 1;
}

.horarios-content {
  margin-top: 20px;
}

.calendars-section {
  margin-top: 20px;
}

.grupo-section {
  margin-bottom: 20px;
}

table {
  width: 100%;
  border-collapse: collapse;
  margin-top: 10px;
}

th, td {
  border: 1px solid #ddd;
  padding: 8px;
  text-align: center;
}

th {
  background-color: #f4f4f4;
}

.registro-row {
  display: flex;
  justify-content: space-between;
  gap: 120px; /* Espaciado entre los formularios */
}

.registro-form {
  flex: 1; /* Cada formulario ocupa el mismo espacio */
  padding: 100px;
  background-color: #ffffff;
  border: 1px solid #ddd;
  border-radius: 8px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
}

.registro-form h1 {
  text-align: center;
  margin-bottom: 20px;
}

.form-group {
  display: flex;
  align-items: center;
  margin-bottom: 15px;
}

.form-group label {
  flex: 1;
  margin-right: 10px;
}

.form-group input,
.form-group select {
  flex: 2;
  padding: 8px;
  border: 1px solid #ccc;
  border-radius: 4px;
}

.input-group {
  display: flex;
  justify-content: space-between;
  gap: 20px; /* Espacio entre los dos inputs */
}

.input-container {
  flex: 1; /* Esto hace que los inputs ocupen espacio de manera equitativa */
}
